body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.wrapper {
  margin: 0 auto;
  max-width: 800px;
}

form,
.sent-m {
  background: #ffffff;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  max-width: 800px;
}

.sent-m-wrap {
  padding: 100px 0;
}

.sent-m-wrap h3,
.drop-modal h3 {
  text-align: center;
  padding: 0 30px;
}

.sent-m-wrap p,
.drop-modal p {
  text-align: center;
  padding: 0 100px;
}

.drop-modal {
  pointer-events: none;
}

.drop {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(255, 255, 255, 0.9);
  border: 1px dashed #cccccc;
  border-radius: 5px;
  margin: 5px;
}

.input-file {
  width: 0;
  height: 0;
  opacity: 0;
  position: absolute;
  overflow: hidden;
  z-index: -1;
}

.label-file {
  color: #0055FB;
  display: inline-block;
  cursor: pointer;
  padding-left: 5px;
}

.attachment {
  border: 1px solid #ced4da;
  margin-top: 5px;
  padding: 5px 5px;
}

.attachment-delete {
  border: none;
  background-color: #ffffff;
  color:  #FF6666;
  text-align: center;
  line-height: 1;
}

table {
  box-sizing: border-box;
  max-width: 800px;
}

table thead {
  border-bottom: 1px solid #dddddd;
}

table th {
  color: #949494;
  font-size: 16px;
  font-weight: normal;
}

.cell-w {
  min-width: 100px;
}

.text-over {
  max-width: 450px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

